<template>
  <div class="location">
    <div class="box">
      <el-form>
        <el-form-item>
          <el-input class="search" placeholder="请输入内容" v-model="searchStr">
            <el-button slot="append" icon="el-icon-search" @click="search"></el-button>
          </el-input>

          <el-button class="subBtn" type="primary" @click="addCategories">添加类别</el-button>
          <el-button
            class="subBtn"
            style="background-color: #f56c6c;border: 0;"
            type="primary"
            @click="deleteSelection"
          >删除选中</el-button>
        </el-form-item>
      </el-form>
      <template>
        <el-table
          :data="categoryList"
          @selection-change="handleSelectionChange"
          style="width: 100%"
        >
          <el-table-column type="selection" width="55"></el-table-column>

          <el-table-column prop="id" label="id">
            <!-- <template slot-scope="scope">
            <img :src="scope.row.picture" height="50" />
            </template>-->
          </el-table-column>
          <el-table-column prop="name" label="类别名称">
            <!-- <template slot-scope="scope">
            <p>{{scope.row.title}}</p>
            </template>-->
          </el-table-column>

          <el-table-column label="操作" width="150">
            <template slot-scope="scope">
              <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
              <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </template>
      <template>
        <el-dialog title="添加类别" :visible.sync="addVisible">
          <el-form :model="articleCategory">
            <el-form-item label="类别名称">
              <el-input v-model="articleCategory.name"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="addVisible=false">取 消</el-button>
            <el-button type="primary" @click="add">确 定</el-button>
          </div>
        </el-dialog>
      </template>
      <template>
        <el-dialog title="添加类别" :visible.sync="editVisible">
          <el-form :model="articleCategory">
            <el-form-item label="类别名称">
              <el-input v-model="articleCategory.name"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="editVisible=false">取 消</el-button>
            <el-button type="primary" @click="edit">确 定</el-button>
          </div>
        </el-dialog>
      </template>
    </div>
    <template>
      <div class="page">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :page-size="page.pageSize"
          layout="total, prev, pager, next"
          :total="page.totalSize"
        ></el-pagination>
      </div>
    </template>
  </div>
</template>

<script>
export default {
  name: "filterTable",
  data() {
    return {
      pageSize: 7,
      page: {},
      searchStr: "",
      addVisible: false,
      editVisible: false,
      multipleSelection: [],
      articleCategory: {
        name: ""
      },
      
      categoryList: []
    };
  },
  created() {
    this.findPage(1, this.pageSize);
  },
  methods: {
    findPage(pageNum, pageSize) {
      this.$axios({
        method: "get",
        // url: "http://localhost:8888/api/login",
        // method: "get",
        url: this.$baseURL + "/articleCategory/findPage",
        params: { pageNum: pageNum, pageSize: pageSize } ,

        headers: {
          "Content-Type": "application/json;charset=UTF-8",
          token: this.$store.getters.token
        }
      })
        .then(response => {
          console.log(response);
          this.page = response.data;

          this.categoryList = response.data.content;
        })
        .catch(error => {
          console.log(error); //请求失败返回的数据
        });
    },
    search() {
      console.log(this.searchStr);
      this.$axios({
        method: "get",
        // url: "http://localhost:8888/api/login",
        // method: "get",
        url: this.$baseURL + "/articleCategory/findBySearchStr",
        params: { searchStr: this.searchStr },
        headers: {
          "Content-Type": "application/json;charset=UTF-8",
          token: this.$store.getters.token
        }
      })
        .then(response => {
          console.log(response);
          this.categoryList = response.data;
        })
        .catch(error => {
          console.log(error); //请求失败返回的数据
          this.$message({
            showClose: true,
            message: error,
            type: "error"
          });
        });
    },

    add() {
      this.addVisible = false;
      console.log("1234567", this.articleCategory);

      this.$axios({
        method: "post",
        // url: "http://localhost:8888/api/login",
        // method: "get",
        url: this.$baseURL + "/articleCategory/add",
        data: this.articleCategory,
        headers: {
          "Content-Type": "application/json;charset=UTF-8",
          token: this.$store.getters.token
        }
      })
        .then(response => {
          console.log(response);
          if (response.data.ok) {
            this.$message({
              showClose: true,
              message: response.data.message,
              type: "success"
            });
            this.findPage(1, this.pageSize);
          } else {
            this.$message({
              showClose: true,
              message: response.data.message,
              type: "error"
            });
          }
        })
        .catch(error => {
          console.log(error); //请求失败返回的数据
          this.$message({
            showClose: true,
            message: error,
            type: "error"
          });
        });

      this.articleCategory = {};
    },
    edit() {
      this.editVisible = false;

      this.$axios({
        method: "put",
        // url: "http://localhost:8888/api/login",
        // method: "get",
        url: this.$baseURL + "/articleCategory/updateById",
        data: this.articleCategory,
        headers: {
          "Content-Type": "application/json;charset=UTF-8",
          token: this.$store.getters.token
        }
      })
        .then(response => {
          console.log(response);
          if (response.data.ok) {
            this.$message({
              showClose: true,
              message: response.data.message,
              type: "success"
            });

            this.findPage(this.page.pageNum, this.pageSize);
          } else {
            this.$message({
              showClose: true,
              message: response.data.message,
              type: "error"
            });
          }
        })
        .catch(error => {
          console.log(error); //请求失败返回的数据
          this.$message({
            showClose: true,
            message: error,
            type: "error"
          });
        });

      this.articleCategory = {};
    },
    addCategories() {
      this.articleCategory = {};
      this.addVisible = true;
      console.log("添加");
    },
    handleEdit(index, row) {
      this.editVisible = true;
      console.log(index, row);
      this.articleCategory = row;
    },
    handleDelete(index, row) {
      this.$axios({
        method: "delete",
        // url: "http://localhost:8888/api/login",
        // method: "get",
        url: this.$baseURL + "/articleCategory/delete",
        data: row,
        headers: {
          "Content-Type": "application/json;charset=UTF-8",
          token: this.$store.getters.token
        }
      })
        .then(response => {
          console.log(response);
          if (response.data.ok) {
            this.$message({
              showClose: true,
              message: response.data.message,
              type: "success"
            });
            
            this.findPage(this.page.pageNum, this.pageSize);
          } else {
            this.$message({
              showClose: true,
              message: response.data.message,
              type: "error"
            });
          }
        })
        .catch(error => {
          console.log(error); //请求失败返回的数据
          this.$message({
            showClose: true,
            message: error,
            type: "error"
          });
        });

      console.log(index, row);
    },
    deleteSelection() {
      console.log(this.multipleSelection);
      for (let index in this.multipleSelection) {
        this.$axios({
          method: "delete",
          // url: "http://localhost:8888/api/login",
          // method: "get",
          url: this.$baseURL + "/articleCategory/delete",
          data: this.multipleSelection[index],
          headers: {
            "Content-Type": "application/json;charset=UTF-8",
            token: this.$store.getters.token
          }
        })
          .then(response => {
            console.log(response);
            if (response.data.ok) {
              this.$message({
                showClose: true,
                message: response.data.message,
                type: "success"
              });
              if (this.page.pageNum > 1) {
                this.page.pageNum = this.page.pageNum - 1;
              }
              this.findPage(this.page.pageNum, this.pageSize);
            } else {
              this.$message({
                showClose: true,
                message: response.data.message,
                type: "error"
              });
            }
          })
          .catch(error => {
            console.log(error); //请求失败返回的数据
            this.$message({
              showClose: true,
              message: error,
              type: "error"
            });
          });
      }
    },
    filterHandler(value, row, column) {
      const property = column["property"];
      return row[property] === value;
    },
    formatter(row, column) {
      return row.address;
    },
    filterTag(value, row) {
      return row.tag === value;
    },
    handleSelectionChange(val) {
      console.log(val);
      this.multipleSelection = val;
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      // this.findPage(val, this.pageSize);
    }
  }
};
</script>

<style scoped>
.box,
.location {
  position: relative;
  height: 94%;
  overflow: scroll;
}
.location {
  /* margin-bottom: 18px; */
  height: 100%;
}
p {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.search {
  width: 70%;
}
.subBtn {
  width: 10%;
  margin: 0 auto;
  float: right;
  margin-left: 10px;
}
.page {
  width: 100%;
  position: absolute;
  bottom: 0;
  background-color: #fff;
  overflow: hidden;
  text-align: center;
}
</style>



